import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { deleteRecipe } from '../../../store/recipes';
import Modal from '../../../common/components/Modal/Modal';
import Button, { POSITIVE, NEGATIVE } from '../../../common/components/Button/Button';
import Heading from '../../../common/components/Heading/Heading';
import AddRecipe from './AddRecipe/AddRecipe';
import ViewRecipe from './View Recipe/ViewRecipe';
import { commonDict, mealRecipesDict } from '../../../common/config/dict';
import styles from './MealRecipes.module.scss';

const ADD_RECIPE = 'ADD_RECIPE';
const VIEW_RECIPE = 'VIEW_RECIPE';

class MealRecipes extends Component {
  constructor(props) {
    super(props);
    this.closeModal = this.closeModal.bind(this);
    this.deleteRecipe = this.deleteRecipe.bind(this);
    this.handleAddRecipe = this.handleAddRecipe.bind(this);
    this.handleViewRecipe = this.handleViewRecipe.bind(this);
    this.state = {
      showModal: false,
      type: ADD_RECIPE,
      recipe: '',
    };
  }

  closeModal() {
    this.setState({ showModal: false });
  }

  deleteRecipe(recipeId) {
    this.props.deleteRecipeInRedux(recipeId);
  }

  handleAddRecipe() {
    this.setState({
      showModal: true,
      type: ADD_RECIPE,
    });
  }

  handleViewRecipe(recipe) {
    this.setState({
      showModal: true,
      type: VIEW_RECIPE,
      recipe,
    });
  }

  render() {
    return (
      <div className={styles.meal}>
        <Heading
          text={this.props.mealName}
          className={styles.heading}
        />
        {
          this.props.recipes
            .filter(recipe => recipe.mealId === this.props.mealId)
            .map(recipe => (
              <div
                key={recipe.id}
                className={styles.recipes}
              >
                <li className={styles['recipe-container']}>
                  <Button
                    onClick={() => this.deleteRecipe(recipe.id)}
                    className={styles['delete-btn']}
                    text={commonDict.buttons.delete}
                    type={NEGATIVE}
                  />
                  <Button
                    onClick={() => this.handleViewRecipe(recipe)}
                    className={styles.recipe}
                    text={recipe.name}
                  />
                </li>
              </div>
            ))
        }
        <Button
          onClick={() => this.handleAddRecipe(true)}
          className={styles['add-recipe-button']}
          text={mealRecipesDict.buttons.add}
          type={POSITIVE}
        />
        <Modal
          shouldShow={this.state.showModal}
          hideModal={this.closeModal}
        >
          {
            this.state.type === ADD_RECIPE
              ? (
                <AddRecipe
                  mealId={this.props.mealId}
                  hideModal={this.closeModal}
                />
              )
              : <ViewRecipe recipe={this.state.recipe} />
          }
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  recipes: state.recipes,
});

const mapDispatchToProps = dispatch => ({
  deleteRecipeInRedux: recipeId => deleteRecipe(dispatch, recipeId),
});


MealRecipes.propTypes = {
  mealId: PropTypes.string.isRequired,
  mealName: PropTypes.string.isRequired,
  recipes: PropTypes.arrayOf(PropTypes.object),
  deleteRecipeInRedux: PropTypes.func,
};

MealRecipes.defaultProps = {
  recipes: [],
  deleteRecipeInRedux: () => true,
};

export default connect(mapStateToProps, mapDispatchToProps)(MealRecipes);
