import React from 'react';
import MealRecipes from './MealRecipes/MealRecipes';
import { meals } from '../../common/consts';
import styles from './Cookbook.module.scss';

function Cookbook() {
  return (
    <div className={styles.cookbook}>
      {
        meals.map(meal => (
          <MealRecipes
            key={meal.id}
            mealName={meal.name}
            mealId={meal.id}
          />
        ))
      }
    </div>
  );
}

export default Cookbook;
