import React, { Component } from 'react';
import Button, { POSITIVE, NEGATIVE } from '../../common/components/Button/Button';
import { timerDict } from '../../common/config/dict';
import TimeUnit from './TimeUnit/TimeUnit';
import TimeUnitInput from './TimeUnitInput/TimeUnitInput';
import styles from './Timer.module.scss';

const BUTTON_START = 'start';
const BUTTON_STOP = 'stop';

const calcHours = time => Math.floor(time / 3600);
const calcMinutes = time => Math.floor((time - calcHours(time) * 3600) / 60);
const calcSeconds = time => time - calcHours(time) * 3600 - calcMinutes(time) * 60;

class Timer extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.startTimer = this.startTimer.bind(this);
    this.stopTimer = this.stopTimer.bind(this);
    this.resetTimer = this.resetTimer.bind(this);
    this.updateCountdown = this.updateCountdown.bind(this);
    this.state = {
      hours: 0,
      minutes: 0,
      seconds: 0,
      buttonShown: BUTTON_START,
    };
  }

  componentWillUnmount() {
    window.clearInterval(this.interval);
  }

  handleChange(e) {
    this.setState({ [e.target.name]: Number(e.target.value) });
  }

  startTimer() {
    const timeSet = this.state.hours * 3600 + this.state.minutes * 60 + this.state.seconds;
    if (timeSet <= 0) return;
    const dateInFuture = (Date.now() / 1000) + timeSet;
    this.interval = window.setInterval(() => this.updateCountdown(dateInFuture), 1000);
    this.setState({ buttonShown: BUTTON_STOP });
  }

  updateCountdown(timeInFuture) {
    const remainingTime = Math.round(timeInFuture - (Date.now() / 1000));
    this.setState({
      hours: calcHours(remainingTime),
      minutes: calcMinutes(remainingTime),
      seconds: calcSeconds(remainingTime),
    });
    if (this.state.hours <= 0 && this.state.minutes <= 0 && this.state.seconds <= 0) {
      this.stopTimer();
    }
  }

  stopTimer() {
    window.clearInterval(this.interval);
    this.setState({ buttonShown: BUTTON_START });
  }

  resetTimer() {
    window.clearInterval(this.interval);
    this.setState({
      hours: 0,
      minutes: 0,
      seconds: 0,
      buttonShown: BUTTON_START,
    });
  }

  render() {
    return (
      <div className={styles.timer}>
        {
          this.state.buttonShown === BUTTON_STOP && (
            <div>
              <TimeUnit
                timeUnit={this.state.hours}
                unitName={timerDict.timeUnits.hours}
              />
              <TimeUnit
                timeUnit={this.state.minutes}
                unitName={timerDict.timeUnits.minutes}
              />
              <TimeUnit
                timeUnit={this.state.seconds}
                unitName={timerDict.timeUnits.seconds}
              />
            </div>
          )
        }
        {
          this.state.buttonShown === BUTTON_START && (
            <div className={styles.inputs}>
              <TimeUnitInput
                min="0"
                max="24"
                onChange={this.handleChange}
                name="hours"
                value={this.state.hours}
                unitName={timerDict.timeUnits.hours}
              />
              <TimeUnitInput
                min="0"
                max="59"
                onChange={this.handleChange}
                name="minutes"
                value={this.state.minutes}
                unitName={timerDict.timeUnits.minutes}
              />
              <TimeUnitInput
                min="0"
                max="59"
                onChange={this.handleChange}
                name="seconds"
                value={this.state.seconds}
                unitName={timerDict.timeUnits.seconds}
              />
            </div>
          )
        }
        <div className={styles.buttons}>
          {
            this.state.buttonShown === BUTTON_START && (
              <Button
                onClick={this.startTimer}
                text={timerDict.buttons.start}
                type={POSITIVE}
                className={styles['start-button']}
              />
            )
          }
          {
            this.state.buttonShown === BUTTON_STOP && (
              <Button
                onClick={this.stopTimer}
                text={timerDict.buttons.stop}
                type={NEGATIVE}
                className={styles['stop-button']}
              />
            )
          }
          <Button
            onClick={this.resetTimer}
            text={timerDict.buttons.reset}
            className={styles['reset-button']}
          />
        </div>
      </div>
    );
  }
}
export default Timer;
