import { getFromLocalStorage, setInLocalStorage } from '../utils/localStorage';

const RECIPES_KEY = 'recipes';

export const getRecipesFromLocalStorage = () => getFromLocalStorage(RECIPES_KEY) || [];
export const addRecipeInLocalStorage = (newRecipe) => {
  const currentRecipes = getRecipesFromLocalStorage();
  const newRecipes = [...currentRecipes, newRecipe];
  setInLocalStorage(RECIPES_KEY, newRecipes);
};
export const setRecipesInLocalStorage = newRecipes => setInLocalStorage(RECIPES_KEY, newRecipes);
