import React from 'react';
import { days } from '../../common/consts';
import DailyGroceryList from './DailyGroceryList/DailyGroceryList';
import styles from './GroceryList.module.scss';

function GroceryList() {
  return (
    <div className={styles['grocery-list']}>
      {
        days.map(day => (
          <DailyGroceryList
            day={day.name}
            key={day.id}
          />
        ))
      }
    </div>
  );
}

export default GroceryList;
