import React, { Component } from 'react';
import connect from 'react-redux/es/connect/connect';
import PropTypes from 'prop-types';
import styles from './MealPlan.module.scss';
import generateId from '../../../../common/utils/id';
import Heading, { H3 } from '../../../../common/components/Heading/Heading';
import Select from '../../../../common/components/Select/Select';
import Button, { NEGATIVE } from '../../../../common/components/Button/Button';
import { commonDict, hintsDict } from '../../../../common/config/dict';
import { addDish, deleteDish } from '../../../../store/dishes';

const getRecipeById = (id, recipes) => recipes.find(recipe => recipe.id === id) || {};
const isRecipeAlreadyAdded = (recipe, dishes) => !!dishes.find(dish => dish.recipeId === recipe.id);

class MealPlan extends Component {
  constructor(props) {
    super(props);
    this.handleDishChoice = this.handleDishChoice.bind(this);
    this.deleteDish = this.deleteDish.bind(this);
  }

  handleDishChoice(recipeId) {
    const newDish = {
      id: generateId('dish'),
      day: this.props.day,
      mealId: this.props.meal.id,
      recipeId,
      name: getRecipeById(recipeId, this.props.recipesOfCurrentMeal).name,
      ingredients: getRecipeById(recipeId, this.props.recipesOfCurrentMeal).ingredients,
    };
    this.props.addDishToRedux(newDish);
  }

  deleteDish(dishId) {
    this.props.deleteDishInRedux(dishId);
  }

  render() {
    const selectOptions = this.props.recipesToChoose.map(
      recipe => ({ value: recipe.id, label: recipe.name }),
    );

    return (
      <div className={styles.meal}>
        <div className={styles['header-container']}>
          <Heading
            type={H3}
            text={this.props.meal.name}
            className={styles['meal-heading']}
          />
        </div>
        <div>
          {
            this.props.todayDishes.map(dish => (
              <div
                key={dish.id}
                className={styles.dish}
              >
                <Button
                  type={NEGATIVE}
                  text={commonDict.buttons.delete}
                  onClick={() => this.deleteDish(dish.id)}
                  className={styles['delete-button']}
                />
                <div className={styles.name}>{dish.name}</div>
              </div>
            ))
          }
          {
            this.props.recipesToChoose.length !== 0 && (
              <Select
                placeholder={commonDict.buttons.plus}
                options={selectOptions}
                onChange={this.handleDishChoice}
                className={styles.select}
              />
            )
          }
          {
            this.props.recipesOfCurrentMeal.length === 0 && (
              <div className={styles.hint}>{hintsDict.noRecipesHint}</div>
            )
          }
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state, props) => {
  const recipesOfCurrentMeal = state.recipes.filter(recipe => recipe.mealId === props.meal.id);
  const todayDishes = state.dishes.filter(
    dish => dish.day === props.day && dish.mealId === props.meal.id,
  );
  const recipesToChoose = recipesOfCurrentMeal.filter(
    recipe => !isRecipeAlreadyAdded(recipe, todayDishes),
  );

  return {
    recipesOfCurrentMeal,
    recipesToChoose,
    todayDishes,
  };
};

const mapDispatchToProps = dispatch => ({
  addDishToRedux: newDish => addDish(dispatch, newDish),
  deleteDishInRedux: dishId => deleteDish(dispatch, dishId),
});

MealPlan.propTypes = {
  recipesOfCurrentMeal: PropTypes.arrayOf(PropTypes.object),
  recipesToChoose: PropTypes.arrayOf(PropTypes.object),
  todayDishes: PropTypes.arrayOf(PropTypes.object),
  day: PropTypes.string,
  meal: PropTypes.objectOf(PropTypes.string),
  addDishToRedux: PropTypes.func,
  deleteDishInRedux: PropTypes.func,
};

MealPlan.defaultProps = {
  recipesOfCurrentMeal: [],
  recipesToChoose: [],
  todayDishes: [],
  day: '',
  meal: {},
  addDishToRedux: () => true,
  deleteDishInRedux: () => true,
};

export default connect(mapStateToProps, mapDispatchToProps)(MealPlan);
