import { getRecipesFromLocalStorage, addRecipeInLocalStorage, setRecipesInLocalStorage } from '../common/services/recipes';
import generateId from '../common/utils/id';

const ADD_RECIPE = 'ADD_RECIPE';
const DELETE_RECIPE = 'DELETE_RECIPE';
const MARK_AS_BOUGHT = 'MARK_AS_BOUGHT';

const initialState = getRecipesFromLocalStorage();

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case ADD_RECIPE:
      return [...state, action.newRecipe];
    case DELETE_RECIPE:
      return action.newRecipes;
    default:
      return state;
  }
}

export function addRecipe(dispatch, newRecipe) {
  const recipeToAdd = { ...newRecipe, id: generateId('recipe') };
  addRecipeInLocalStorage(recipeToAdd);
  dispatch({ type: ADD_RECIPE, newRecipe: recipeToAdd });
}

export function deleteRecipe(dispatch, recipeId) {
  const newRecipes = getRecipesFromLocalStorage().filter(recipe => recipe.id !== recipeId);
  setRecipesInLocalStorage(newRecipes);
  dispatch({ type: DELETE_RECIPE, newRecipes });
}


export function markAsBought(dispatch, ingredientId) {
  const recipeContainingBoughtIngredient = getRecipesFromLocalStorage().filter(
    recipe => recipe.ingredients.find(ingredient => ingredient.id === ingredientId),
  );
  recipeContainingBoughtIngredient.isBought = true;
  dispatch({ type: MARK_AS_BOUGHT, recipeContainingBoughtIngredient });
}
