import React from 'react';
import PropTypes from 'prop-types';
import Heading, { H2 } from '../../../common/components/Heading/Heading';
import styles from './TimeUnitInput.module.scss';

function TimeUnitInput(props) {
  return (
    <div className={styles.container}>
      <input
        type="number"
        min={props.min}
        max={props.max}
        onChange={props.onChange}
        name={props.name}
        value={props.value}
        className={styles['time-input']}
      />
      <Heading
        text={props.unitName}
        type={H2}
        className={styles.heading}
      />
    </div>
  );
}

TimeUnitInput.propTypes = {
  min: PropTypes.string,
  max: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  name: PropTypes.string,
  value: PropTypes.number.isRequired,
  unitName: PropTypes.string,
};

TimeUnitInput.defaultProps = {
  min: '',
  max: '',
  name: '',
  unitName: '',
};
export default TimeUnitInput;
