import React, { Component } from 'react';
import Navbar from './common/components/Navbar/Navbar';
import Home from './screens/Home/Home';
import Cookbook from './screens/Cookbook/Cookbook';
import MealPlanning from './screens/MealPlanning/MealPlanning';
import GroceryList from './screens/GroceryList/GroceryList';
import Timer from './screens/Timer/Timer';
import { navDict } from './common/config/dict';

const linksObj = [
  { id: 'home', text: navDict.links.home, component: <Home /> },
  { id: 'cookbook', text: navDict.links.cookbook, component: <Cookbook /> },
  { id: 'plannig', text: navDict.links.planning, component: <MealPlanning /> },
  { id: 'groceryList', text: navDict.links.groceryList, component: <GroceryList /> },
  { id: 'timer', text: navDict.links.timer, component: <Timer /> },
];

const getComponentBasingOnId = (linkId = '', links = []) => {
  const currentLinkObj = links.find(linkObj => linkObj.id === linkId);
  return currentLinkObj.component;
};

class App extends Component {
  constructor(props) {
    super(props);
    this.handleNavLinkClick = this.handleNavLinkClick.bind(this);
    this.state = {
      currentLinkId: 'home',
    };
  }

  handleNavLinkClick(clickedLink) {
    this.setState({ currentLinkId: clickedLink });
  }

  render() {
    return (
      <div>
        <Navbar
          links={linksObj}
          onLinkClick={this.handleNavLinkClick}
          currentLinkId={this.state.currentLinkId}
        />
        <div>
          {
            getComponentBasingOnId(this.state.currentLinkId, linksObj)
          }
        </div>
      </div>
    );
  }
}

export default App;
