import React from 'react';
import PropTypes from 'prop-types';
import Heading, { H2 } from '../../../common/components/Heading/Heading';
import styles from './TimeUnit.module.scss';

function TimeUnit(props) {
  return (
    <div className={styles.container}>
      <div className={styles['time-unit']}>
        {props.timeUnit}
      </div>
      <Heading
        text={props.unitName}
        type={H2}
        className={styles.heading}
      />
    </div>
  );
}

TimeUnit.propTypes = {
  timeUnit: PropTypes.number.isRequired,
  unitName: PropTypes.string.isRequired,
};

export default TimeUnit;
