import { getDishesFromLocalStorage, addDishInLocalStorage, setDishesInLocalStorage } from '../common/services/dishes';
import generateId from '../common/utils/id';

const ADD_DISH = 'ADD_DISH';
const UPDATE_DISHES = 'UPDATE_DISHES';

const initialState = getDishesFromLocalStorage();

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case ADD_DISH:
      return [...state, action.newDish];
    case UPDATE_DISHES:
      return action.newDishes;
    default:
      return state;
  }
}

export function addDish(dispatch, newDish) {
  const dishToAdd = { ...newDish, id: generateId('dish') };
  addDishInLocalStorage(dishToAdd);
  dispatch({ type: ADD_DISH, newDish: dishToAdd });
}

export function deleteDish(dispatch, dishId) {
  const newDishes = getDishesFromLocalStorage().filter(dish => dish.id !== dishId);
  setDishesInLocalStorage(newDishes);
  dispatch({ type: UPDATE_DISHES, newDishes });
}

export function updateDishes(dispatch, updatedDish) {
  const newDishes = getDishesFromLocalStorage().map(dish => (
    dish.id === updatedDish.id
      ? updatedDish
      : dish
  ));
  setDishesInLocalStorage(newDishes);
  dispatch({ type: UPDATE_DISHES, newDishes });
}
