import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import styles from './Button.module.scss';

export const POSITIVE = 'positive';
export const NEGATIVE = 'negative';

const createButtonModifier = modifier => styles[`button--${modifier}`];
const generateTypeClass = (type) => {
  switch (type) {
    case POSITIVE:
      return createButtonModifier('positive');
    case NEGATIVE:
      return createButtonModifier('negative');
    default:
      return createButtonModifier('neutral');
  }
};

function Button(props) {
  return (
    <button
      type="button"
      onClick={props.onClick}
      className={cn(
        styles.button,
        generateTypeClass(props.type),
        props.className,
      )}
    >
      {props.text}
    </button>
  );
}

Button.propTypes = {
  onClick: PropTypes.func.isRequired,
  className: PropTypes.string,
  type: PropTypes.string,
  text: PropTypes.string,
};

Button.defaultProps = {
  className: styles.button,
  type: 'neutral',
  text: '',
};

export default Button;
