import { getFromLocalStorage, setInLocalStorage } from '../utils/localStorage';

const DISHES_KEY = 'dishes';

export const getDishesFromLocalStorage = () => getFromLocalStorage(DISHES_KEY) || [];
export const addDishInLocalStorage = (newDish) => {
  const currentDishes = getDishesFromLocalStorage();
  const newDishes = [...currentDishes, newDish];
  setInLocalStorage(DISHES_KEY, newDishes);
};
export const setDishesInLocalStorage = newDishes => setInLocalStorage(DISHES_KEY, newDishes);
