import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import generateId from '../../../../common/utils/id';
import { addRecipeDict, validationDict } from '../../../../common/config/dict';
import { addRecipe } from '../../../../store/recipes';
import { isIngredientNameValid, isRecipeNameValid, isIngredientsAmountValid } from '../../../../common/utils/validation';
import Heading, { H3 } from '../../../../common/components/Heading/Heading';
import Button, { POSITIVE, NEGATIVE } from '../../../../common/components/Button/Button';
import TextInput from '../../../../common/components/TextInput/TextInput';
import WithError from '../../../../common/components/WithError/WithError';
import styles from './AddRecipe.module.scss';


const initialState = {
  editingIngredient: { name: '', id: '' },
  ingredients: [],
  recipeName: '',
  notes: '',
  recipeNameError: false,
  ingredientError: false,
  ingredientsAmountError: false,
};

class AddRecipe extends Component {
  constructor(props) {
    super(props);
    this.removeIngredient = this.removeIngredient.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleEditingIngredient = this.handleEditingIngredient.bind(this);
    this.handleAddIngredient = this.handleAddIngredient.bind(this);
    this.handleSaveRecipe = this.handleSaveRecipe.bind(this);
    this.validateRecipeName = this.validateRecipeName.bind(this);
    this.validateIngredientsAmount = this.validateIngredientsAmount.bind(this);
    this.clear = this.clear.bind(this);
    this.state = initialState;
  }

  handleChange(event) {
    this.setState({
      [event.target.name]: event.target.value,
      recipeNameError: false,
    });
  }

  handleEditingIngredient(event) {
    this.setState({
      editingIngredient: { name: event.target.value, id: generateId('ingredient') },
      ingredientError: false,
      ingredientsAmountError: false,
    });
  }

  handleAddIngredient() {
    this.setState(prevState => (
      isIngredientNameValid(prevState.editingIngredient.name)
        ? {
          ingredients: [...prevState.ingredients, prevState.editingIngredient],
          editingIngredient: { name: '', id: '' },
        }
        : { ingredientError: true }
    ));
  }

  removeIngredient(index) {
    this.setState(prevState => ({
      ingredients: prevState.ingredients.filter((ingredient, idx) => index !== idx),
    }));
  }

  validateRecipeName() {
    return !isRecipeNameValid(this.state.recipeName)
      ? this.setState({ recipeNameError: true })
      : true;
  }

  validateIngredientsAmount() {
    return !isIngredientsAmountValid(this.state.ingredients)
      ? this.setState({ ingredientsAmountError: true })
      : true;
  }

  handleSaveRecipe() {
    const saveRecipe = () => {
      const newRecipe = {
        mealId: this.props.mealId,
        name: this.state.recipeName,
        ingredients: this.state.ingredients,
        notes: this.state.notes,
      };
      this.validateRecipeName();
      this.validateIngredientsAmount();
      this.props.addRecipeInRedux(newRecipe);
      this.props.hideModal(false);
      this.setState(initialState);
    };
    if (this.validateRecipeName() && this.validateIngredientsAmount()) saveRecipe();
  }

  clear() {
    this.setState(initialState);
  }

  render() {
    return (
      <div>
        <form>
          <WithError
            hasError={this.state.recipeNameError}
            message={validationDict.recipeNameErrorMessage}
          >
            <TextInput
              className={styles['recipe-name']}
              placeholder={addRecipeDict.inputs.recipeName}
              name="recipeName"
              onChange={this.handleChange}
              value={this.state.recipeName}
            />
          </WithError>
          <div>
            <WithError
              hasError={this.state.ingredientsAmountError}
              message={validationDict.ingredientsAmountErrorMessage}
            >
              <Heading
                type={H3}
                text={addRecipeDict.recipe.ingredients}
                className={styles['ingredients-heading']}
              />
            </WithError>
            <ul className={styles['ingredients-list']}>
              {
                this.state.ingredients.map((ingredient, idx) => (
                  <li
                    key={ingredient.id}
                    className={styles.ingredient}
                  >
                    <Button
                      onClick={() => this.removeIngredient(idx)}
                      className={styles['delete-btn']}
                      text={addRecipeDict.buttons.delete}
                      type={NEGATIVE}
                    />
                    {ingredient.name}
                  </li>
                ))
                }
            </ul>
            <div className={styles['add-ingredient']}>
              <Button
                buttonType="submit"
                className={styles['add-ingredient-button']}
                text={addRecipeDict.buttons.add}
                onClick={this.handleAddIngredient}
                type={POSITIVE}
              />
              <WithError
                hasError={this.state.ingredientError}
                message={validationDict.ingredientErrorMessage}
              >
                <TextInput
                  className={styles['ingredient-field']}
                  onChange={this.handleEditingIngredient}
                  value={this.state.editingIngredient.name}
                  name="editingIngredient"
                />
              </WithError>
            </div>
          </div>
          <div>
            <Heading
              type={H3}
              text={addRecipeDict.recipe.notes}
              className={styles['notes-heading']}
            />
            <textarea
              className={styles.notes}
              onChange={this.handleChange}
              value={this.state.notes}
              placeholder={addRecipeDict.inputs.notes}
              name="notes"
            />
          </div>
        </form>
        <Button
          onClick={this.handleSaveRecipe}
          className={styles['save-button']}
          text={addRecipeDict.buttons.save}
          type={POSITIVE}
        />
        <Button
          onClick={this.clear}
          className={styles['clear-button']}
          text={addRecipeDict.buttons.clear}
          type={NEGATIVE}
        />
      </div>
    );
  }
}

const mapStateToProps = () => ({});

const mapDispatchToProps = dispatch => ({
  addRecipeInRedux: newRecipe => addRecipe(dispatch, newRecipe),
});

AddRecipe.propTypes = {
  mealId: PropTypes.string.isRequired,
  addRecipeInRedux: PropTypes.func,
  hideModal: PropTypes.func.isRequired,
};

AddRecipe.defaultProps = {
  addRecipeInRedux: () => true,
};

export default connect(mapStateToProps, mapDispatchToProps)(AddRecipe);
