import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { updateDishes } from '../../../store/dishes';
import { hintsDict } from '../../../common/config/dict';
import Heading, { H2 } from '../../../common/components/Heading/Heading';
import styles from './DailyGroceryList.module.scss';

const toggleIsIngredientBought = (currentIngredients, ingredientToBeToggled) => (
  currentIngredients.map(ingredient => (
    ingredient.id === ingredientToBeToggled.id
      ? { ...ingredient, isBought: !ingredient.isBought }
      : ingredient
  ))
);

class DailyGroceryList extends Component {
  constructor(props) {
    super(props);
    this.handleIngredientCheck = this.handleIngredientCheck.bind(this);
  }

  handleIngredientCheck(ingredient, dish) {
    const updatedDish = {
      ...dish,
      ingredients: toggleIsIngredientBought(dish.ingredients, ingredient),
    };
    this.props.updateDishesInRedux(updatedDish);
  }

  render() {
    const todayDishes = this.props.dishes.filter(dish => dish.day === this.props.day);

    return (
      <div className={styles['daily-list']}>
        <Heading
          type={H2}
          text={this.props.day}
          className={styles['day-heading']}
        />
        {
          todayDishes.length
            ? <div className={styles.hint}>{hintsDict.noMealsPlanned}</div>
            : (
              todayDishes.map(dish => (
                dish.ingredients.map(ingredient => (
                  <div
                    key={ingredient.id}
                    className={styles['ingredient-container']}
                  >
                    <input
                      type="checkbox"
                      checked={ingredient.isBought}
                      className={styles['ingredient-checkbox']}
                      onChange={() => this.handleIngredientCheck(ingredient, dish)}
                    />
                    <span>{ingredient.name}</span>
                  </div>
                ))))
            )
        }
      </div>
    );
  }
}

const mapStateToProps = state => ({
  dishes: state.dishes,
});

const mapDispatchToProps = dispatch => ({
  updateDishesInRedux: updatedDish => updateDishes(dispatch, updatedDish),
});

DailyGroceryList.propTypes = {
  day: PropTypes.string.isRequired,
  dishes: PropTypes.arrayOf(PropTypes.object),
  updateDishesInRedux: PropTypes.func.isRequired,
};

DailyGroceryList.defaultProps = {
  dishes: {},
};

export default connect(mapStateToProps, mapDispatchToProps)(DailyGroceryList);
