import React from 'react';
import DayPlan from './DayPlan/DayPlan';
import { days } from '../../common/consts';
import styles from './MealPlanning.module.scss';


function MealPlanning() {
  return (
    <div className={styles['meal-planning']}>
      {
        days.map(day => (
          <DayPlan
            dayName={day.name}
            key={day.id}
          />
        ))
      }
    </div>
  );
}

export default MealPlanning;
